import React from 'react';
import gql from 'graphql-tag';
import { Query } from 'react-apollo';
import Link from '../components/Link';
import Layout from '../components/layouts'

import Loading from '../components/Loading';
import ErrorMessage from '../components/Error';

const GET_CONTRIBUTED_REPOSITORIES = gql`
  {
  	user(login: "ehabdevel") {
	  id
	  repositoriesContributedTo(last: 9) {
	    edges {
	      node {
	        id
	        nameWithOwner
	        url
	        pushedAt
	        homepageUrl
	        description
	      }
	    }
	  }
	} 
  }
`;

const Contrrepos = () => (
  <Layout>
	  <Query query={GET_CONTRIBUTED_REPOSITORIES}>
	    {({ data, loading, error }) => {
	      if (error) {
	        return <ErrorMessage error={error} />;
	      }

	      const { user } = data;

	      if (loading || !user) {
	        return <Loading />;
	      }

	      return (
	      	<div>
			  	<div>
				  	<div className="col-sm-12 text-center p-3 mb-3" style={{backgroundColor: '#eee',}}>
					  	<p className="h2">Repositories I had the honor to contribute to with Github</p>
				  	</div>
				  	<div className="w-sm-100">
		      		{user.repositoriesContributedTo.edges.map(({ node }) => (
						<div key={node.id} className="card m-3 animated fadeInRight">
							<div className="card-header">
					          <Link href={node.url}>
						          <p className="d-sm-flex justify-content-between align-items-center sm">
						            {node.nameWithOwner}{" "}
						            <span className="badge badge-light badge-pill">— {node.pushedAt}</span>
						          </p>
					          </Link>
					        </div>
				          <div className="card-body">
					          <p><strong>Homepage Url:</strong> <Link href={node.homepageUrl}>{node.homepageUrl}</Link></p>
					          <p><strong>Description:</strong> {node.description}</p>
				          </div>
		      			</div>
		      		))}
		      		</div>
		      	</div>
	      	</div>
	      	);

	    }}
	  </Query>
  </Layout>
);

export default Contrrepos;